import React from "react";
import Header from "../../../../components/header/Header";
import SpecialFooter from "../../../../components/specialFooter/SpecialFooter";
import '../../../../styles/pages/projects/ProjectsPage.css'
import bugsBronny from '../../../../images/personal/bugsBronny.png'
import js from "../../../../images/skills/js.png";
import nodejs from "../../../../images/skills/nodejs.png";
import Tags from "../../../../components/tags/Tags";

function DiscordBotBugsBronny() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"Discord Bot Bugs Bronny"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container post">
                    <article>
                        <header>
                            <div className="post-title">
                                <h1 className="title">Personal - Discord Bot Bugs Bronny</h1>
                            </div>
                            <div className="post-meta">
                                <div className="date">
                                    <span className="posted-on font-size-word"><i className="fa fa-calendar"
                                                                                  aria-hidden="true"/><time
                                        className="font-size-word"
                                        dateTime="2021-03-24T05:52:42-05:00">Aug 04, 2020</time></span>
                                    <span className="reading-time font-size-word"><i className="fa fa-clock-o"
                                                                                     aria-hidden="true"/>3-minute read</span>
                                </div>
                                <br/>
                            </div>
                        </header>
                        <div>
                            <h1>Project Description</h1>
                            <p>The objective of the project was to develop a Discord Bot
                                to be used on a Discord Server that I had with a friend.
                                The commands were about Music, Moderation and Fun.
                                <br/>
                                <br/>
                                All code developed for this project is available in a repository on my Github Account
                                under the name
                                <a href="https://github.com/GJordao12/DiscordBot-BugsBronny"
                                   aria-label="Fiverr Platform"
                                   target="_blank" rel="noreferrer"> DiscordBot-BugsBronny</a>.</p>

                            <h2 className="subtitle">Commands that only admins/mods can use:</h2>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!clear 10</mark>:
                                Clear chat (deletes the last 10 messages, including "!clear 10");
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!mute @someone
                                [time]</mark>: Mute a person for a certain period of time (1s/1min/1hour/1day, both in
                                chat and voice chat);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!kick
                                @someone</mark>: Kick someone;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!ban
                                @someone</mark>: Ban someone (but delete messages);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!softban
                                @someone</mark>: Ban and take it off (to delete all messages that person sent);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!unmute
                                @someone</mark>: Unmute (both in chat and voice chat);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!warn
                                @someone</mark>: Leave a "mark" on the person (after 3 catch ban);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!warnings
                                @someone</mark>: Lets you see how many "marks" the person has;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!delwarn
                                @someone</mark>: Takes 1 "warning" from the person;
                            </p>

                            <h2 className="subtitle">Commands that anyone can use:</h2>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!play/!p
                                [link]</mark>: Play music (only with youtube links);
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!play/!p [song
                                name]</mark>: The bot searches for the first 5 results on youtube and then the person
                                does "!play/!p 3" to choose the 3rd option;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!leave</mark>:
                                For the bot leave the voice channel;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!np</mark>: Shows
                                the song the bot is playing;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!skip</mark>:
                                Skips to the next song in the queue;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!cq</mark>:
                                Clears the queue;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!queue</mark>:
                                Shows the queue;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!pug</mark>: A
                                picture of a random pug appears;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!flip</mark>:
                                Throws a coin and "It's Heads" or "It's Tails" comes out;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!avatar
                                @someone</mark>: Shows the avatar of the tag person;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>!avatar</mark>:
                                Shows the person's own avatar;
                            </p>

                            <h2 className="subtitle">Role Based on Number of Messages:</h2>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Top Pog</mark>:
                                Has 100,000 messages on the server;
                            </p>
                            <p className="text-subtitle"> <mark
                                style={{'background-color': 'grey'}}>@Remarkable</mark>: Has 75,000 messages on the
                                server;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Something
                                Else</mark>: Has 50,000 messages on the server;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Too
                                Addicted</mark>: Has 25,000 messages on the server;
                            </p>
                            <p className="text-subtitle"> <mark
                                style={{'background-color': 'grey'}}>@Dedicated</mark>: Has 10,000 messages on the
                                server;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Active</mark>:
                                Has 5,000 messages on the server;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Rookie</mark>:
                                Has 1000 messages on the server [ Images / Commands / Non-server emotes included ];
                            </p>
                            <p className="text-subtitle">(Whenever you go to the next level, you lost the old role)</p>

                            <h2 className="subtitle">Role Based on Time on Server:</h2>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Racoon</mark>:
                                Being on the server for over 2 years;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Veteran</mark>:
                                Joined the server 1 Year ago;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Friendly</mark>:
                                Being in the Community for 6 Months in a Row;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>@Member</mark>:
                                Given at server entrance;
                            </p>
                            <p className="text-subtitle">(Whenever you go to the next level, you lost the old role)</p>

                            <h1>Programming Languages and Tools</h1>
                            <div className="languages-tools">
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={js} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">JavaScript</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={nodejs} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">Node.js</span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1>Media</h1>
                            <img src={bugsBronny} alt="alt"/>
                        </div>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default DiscordBotBugsBronny;