import '../../styles/pages/aboutMe/AboutMe.css'
import React from "react";
import Header from "../../components/header/Header";
import Tags from "../../components/tags/Tags";
import SpecialFooter from "../../components/specialFooter/SpecialFooter.js";
import MenuOption from '../../components/menu/MenuOption';
import Menu from '../../components/menu/Menu';


function AboutMe() {
    return (
        <body class="colorscheme-auto">
            <Tags title="About Me" />
            <main className="wrapper">
                <Header />
                <Menu text="About Me" options={[<MenuOption path="profile" text="Profile" />,
                <MenuOption path="skills" text="Skills" />,
                <MenuOption path="timeline" text="Timeline" />]} />
                <SpecialFooter />
            </main>
        </body>
    )
}

export default AboutMe;