import '../../styles/pages/aboutMe/Timeline.css'
import React from "react";
import Header from "../../components/header/Header";
import SpecialFooter from "../../components/specialFooter/SpecialFooter";
import isep from '../../images/timeLine/ISEP.png'
import fiverr from '../../images/timeLine/fiverr.svg'
import itSector from '../../images/timeLine/ITSector.png'
import Tags from "../../components/tags/Tags";

function Timeline() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"Timeline"}/>
        <main className="wrapper">
            <Header/>

            <div className="container-title">
                <div className="titles">
                    <h4>Timeline</h4>
                </div>
            </div>
            <div className="content">
                <section className="container centered">
                    <section id="cd-timeline" className="cd-container">
                        <div className="cd-timeline-block">
                            <div className="cd-timeline-img ">
                                <img className="img-timeline" src={itSector} alt=""/>
                            </div>
                            <div className="cd-timeline-content ">
                                <h2 style={{color: 'black'}}>September 2022 - Present</h2>
                                <p>
                                    .NET Developer at
                                    <a href="https://www.itsector.pt/" aria-label="ITSector"
                                       target="_blank "> ITSector</a>.
                                </p>
                            </div>
                        </div>
                        <div className="cd-timeline-block">
                            <div className="cd-timeline-img ">
                                <img className="img-timeline" src={itSector} alt=""/>
                            </div>
                            <div className="cd-timeline-content ">
                                <h2 style={{color: 'black'}}>February 2022 - June 2022</h2>
                                <p>
                                    .NET Developer Internship at
                                    <a href="https://www.itsector.pt/" aria-label="ITSector"
                                       target="_blank "> ITSector</a>.
                                </p>
                            </div>
                        </div>
                        <div className="cd-timeline-block">
                            <div className="cd-timeline-img ">
                                <img className="img-timeline" src={fiverr} alt=""/>
                            </div>
                            <div className="cd-timeline-content ">
                                <h2 style={{color: 'black'}}>September 2020 - Present</h2>
                                <p>
                                    Freelancer on the
                                    <a href="https://fiverr.com/gjordao" aria-label="Fiverr Platform "
                                       target="_blank "> Fiverr Platform</a>.
                                </p>
                            </div>
                        </div>
                        <div className="cd-timeline-block">
                            <div className="cd-timeline-img">
                                <img className="img-timeline" src={isep} alt=""/>
                            </div>
                            <div className="cd-timeline-content">
                                <h2 style={{color: 'black'}}>September 2019 - September 2022</h2>
                                <p>
                                    Degree in Computer Engineering at
                                    <a href="https://www.isep.ipp.pt/" aria-label="ISEP" target="_blank"
                                       rel="noreferrer"> ISEP</a> (Instituto Superior de Engenharia do Porto).
                                </p>
                            </div>
                        </div>
                    </section>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Timeline;