import React from "react";
import {Helmet} from "react-helmet";

function Tags(props) {
    return (
        <Helmet>
            <title content={"GJORDAO - " + props.title}>{"GJORDAO - " + props.title}</title>
            <meta property="og:description" content={"Gonalo Jordo - " + props.title}/>
        </Helmet>
    )
}

export default Tags;