import React from "react";
import Header from "../../components/header/Header";
import SpecialFooter from "../../components/specialFooter/SpecialFooter";
import Tags from "../../components/tags/Tags";
import MenuOption from '../../components/menu/MenuOption';
import Menu from '../../components/menu/Menu';

function Projects() {
    return (
        <body class="colorscheme-auto">
            <Tags title="Projects" />
            <main className="wrapper">
                <Header />
                <Menu text="Projects" options={[<MenuOption path="university" text="University" />,
                <MenuOption path="fiverr" text="Fiverr" />,
                <MenuOption path="personal" text="Personal" />]} />
                <SpecialFooter />
            </main>
        </body>
    )
}

export default Projects;