import React from "react";
import Header from "../../../components/header/Header";
import SpecialFooter from "../../../components/specialFooter/SpecialFooter";
import '../../../styles/pages/projects/ProjectsMenu.css'
import Tags from "../../../components/tags/Tags";
import ProjectsMenu from "../../../components/projects/ProjectsMenu";
import ProjectOption from "../../../components/projects/ProjectOption";


function PersonalProjects() {
    return (
        <body class="colorscheme-auto">
            <Tags title={"Personal Projects"} />
            <main className="wrapper">
                <Header />
                <ProjectsMenu title="Personal Projects" projects={[<ProjectOption date="May 05, 2020" path="flappy-bird-game" title="Flappy Bird Game" />,
                <ProjectOption date="Aug 04, 2020" path="discord-bot-bugs-bronny" title="Discord Bot Bugs
                Bronny" />,
                <ProjectOption date="Present" path="my-cv-website" title="My CV Website" />]} />
                <SpecialFooter />
            </main>
        </body>
    )
}

export default PersonalProjects;