import React from "react";
import Header from "../../../components/header/Header";
import SpecialFooter from "../../../components/specialFooter/SpecialFooter";
import '../../../styles/pages/projects/ProjectsMenu.css'
import Tags from "../../../components/tags/Tags";
import ProjectsMenu from "../../../components/projects/ProjectsMenu";
import ProjectOption from "../../../components/projects/ProjectOption";

function UniversityProjects() {
    return (
        <body class="colorscheme-auto">
            <Tags title={"University Projects"} />
            <main className="wrapper">
                <Header />
                <ProjectsMenu title="University Projects" projects={[<ProjectOption date="Nov 22, 2019" path="website-earth-app" title="Website Earth App" />,
                <ProjectOption date="Dec 22, 2019" path="time-series-analysis" title="Java Application for
                Time Series Analysis" />,
                <ProjectOption date="Jun 13, 2020" path="freelancer-java-application" title="Java Application
                to Hire Freelancers and Make Payments"/>,
                <ProjectOption date="Dec 22, 2020" path="pharmaceutical-java-service" title="Java Service for
                a Pharmaceutical Company" />,
                <ProjectOption date="Jun 07, 2021" path="helpdesk-aService" title="Helpdesk aService" />,
                <ProjectOption date="Oct 18, 2021" path="interactive-watch" title="Interactive Watch" />,
                <ProjectOption date="Nov 02, 2021" path="pong-game" title="Pong Game" />,
                <ProjectOption date="Jan 23, 2022" path="social-network-game" title="Social Network Game" />]} />
                <SpecialFooter />
            </main>
        </body>
    )
}

export default UniversityProjects;