import SpecialFooter from "../../components/specialFooter/SpecialFooter";

import Header from "../../components/header/Header";
import '../../styles/pages/aboutMe/Profile.css'
import Tags from "../../components/tags/Tags";
import React from "react";

function Profile() {
    return (
        <body className="colorscheme-auto" onLoad="">
        <Tags title={"Profile"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container">
                    <article>
                        <header>
                            <h1>Profile</h1>
                        </header>
                        <i className="font-size-word fa-calendar calendar-emoji" aria-hidden="true"/>
                        <p className="font-size-word display-inline">August 07, 2001</p>
                        <i className="font-size-word fa-map-pin pin-emoji" aria-hidden="true"/>
                        <p className="font-size-word display-inline">Portugal</p>
                        <br/><br/>
                        <p>
                            My name is Gonalo Jordo and I started
                            studying programming in 2019, when I entered in the Computer Engineering course at <a
                            href="https://www.isep.ipp.pt/" aria-label="ISEP" target="_blank"
                            rel="noreferrer">ISEP</a> (Instituto
                            Superior de Engenharia do Porto). <br/><br/>
                            After finishing my 1st year of the course I decided to
                            enter the working world (whilst I continued to study), so I started working as a Freelancer
                            on the
                            <a href="https://fiverr.com/gjordao" aria-label="Fiverr Platform" target="_blank"
                               rel="noreferrer"> Fiverr
                                Platform</a>. On that Platform I have done and I continue to do several projects, some
                            simpler others more complex, for people who need it and request it.<br/><br/>
                            I'm always looking for new challenges and my goal is to gain professional experience to improve
                            my programming skills as much as possible! So if you have a project and you need help or you want
                            me to be part of your company just get in touch with me :))
                        </p>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Profile;