import React from "react";
import Header from "../../../../components/header/Header";
import SpecialFooter from "../../../../components/specialFooter/SpecialFooter";
import '../../../../styles/pages/projects/ProjectsPage.css'
import CVWebsite from '../../../../images/personal/CVWebsite.png'
import html from "../../../../images/skills/html.png";
import reactjs from "../../../../images/skills/react.png";
import css from "../../../../images/skills/CSS.png";
import js from "../../../../images/skills/js.png";
import github from "../../../../images/skills/github.png";
import Tags from "../../../../components/tags/Tags";

function MyCVWebsite() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"My CV Website"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container post">
                    <article>
                        <header>
                            <div className="post-title">
                                <h1 className="title">Personal - My CV Website</h1>
                            </div>
                            <div className="post-meta">
                                <div className="date">
                                    <span className="posted-on font-size-word"><i className="fa fa-calendar"
                                                                                  aria-hidden="true"/><time
                                        className="font-size-word"
                                        dateTime="2021-03-24T05:52:42-05:00">Present</time></span>
                                    <span className="reading-time font-size-word"><i className="fa fa-clock-o"
                                                                                     aria-hidden="true"/>1-minute read</span>
                                </div>
                                <br/>
                            </div>
                        </header>
                        <div>
                            <h1>Project Description</h1>
                            <p>One of the projects that gave me the most pleasure was making my own CV Website. Make a
                                website where
                                I can share with people who I am, what I work with, and what work I've done. During this
                                process, not
                                only building the website but also making the website visible to everyone through GitHub
                                Pages, I feel
                                I learned a lot and managed to improve my programming skills.
                            </p>

                            <h1>Programming Languages and Tools</h1>
                            <div className="languages-tools">
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={html} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">HTML</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={reactjs} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">ReactJS</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={css} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">CSS</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={js} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">JavaScript</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={github} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">GitHub</span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1>Media</h1>
                            <img src={CVWebsite} alt="alt"/>
                        </div>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default MyCVWebsite;