import React from "react";
import Header from "../../../../components/header/Header";
import SpecialFooter from "../../../../components/specialFooter/SpecialFooter";
import '../../../../styles/pages/projects/ProjectsPage.css'
import java from "../../../../images/skills/java.png";
import c from "../../../../images/skills/C.png";
import sql from "../../../../images/skills/sql.png";
import assembly from "../../../../images/skills/asm.png";
import Tags from "../../../../components/tags/Tags";

function PharmaceuticalCompany() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"Java Service for a Pharmaceutical Company"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container post">
                    <article>
                        <header>
                            <div className="post-title">
                                <h1 className="title">University - Java Service for a Pharmaceutical Company</h1>
                            </div>
                            <div className="post-meta">
                                <div className="date">
                                    <span className="posted-on font-size-word"><i className="fa fa-calendar"
                                                                                  aria-hidden="true"/><time
                                        className="font-size-word"
                                        dateTime="2021-03-24T05:52:42-05:00">Dec 22, 2020</time></span>
                                    <span className="reading-time font-size-word"><i className="fa fa-clock-o"
                                                                                     aria-hidden="true"/>1-minute read</span>
                                </div>
                                <br/>
                            </div>
                        </header>
                        <div>
                            <h1>Project Description</h1>
                            <p>The aim of the project was to develop software that would support the supply of
                                pharmaceutical products. In this case, it would be used primarily to administer
                                self-injectable SARS-CoV-2 vaccines to clients.
                                This service should allow the management of customers, products, orders, deliveries,
                                etc.
                                <br/>
                                <br/>
                                All code developed for this project is available in a repository on my Github Account
                                under the name
                                <a href="https://github.com/GJordao12/ISEP-LAPR3" aria-label="Fiverr Platform"
                                   target="_blank" rel="noreferrer"> ISEP-LAPR3</a>.</p>

                            <h1>Programming Languages and Tools</h1>
                            <div className="languages-tools">
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={java} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">Java</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={c} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">C</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={assembly} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">Assembly</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={sql} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">PL/SQL</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default PharmaceuticalCompany;