import React from "react";
import Header from "../../../../components/header/Header";
import SpecialFooter from "../../../../components/specialFooter/SpecialFooter";
import '../../../../styles/pages/projects/ProjectsPage.css'
import discordPicture from '../../../../images/fiverr/discord-bot-aylog.png'
import js from "../../../../images/skills/js.png";
import nodejs from "../../../../images/skills/nodejs.png";
import Tags from "../../../../components/tags/Tags";

function DiscordBotAylog() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"Discord Bot Aylog"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container post">
                    <article>
                        <header>
                            <div className="post-title">
                                <h1 className="title">Fiverr - Discord Bot Aylog</h1>
                            </div>
                            <div className="post-meta">
                                <div className="date">
                                    <span className="posted-on font-size-word"><i className="fa fa-calendar"
                                                                                  aria-hidden="true"/><time
                                        className="font-size-word"
                                        dateTime="2021-03-24T05:52:42-05:00">Jun 29, 2021</time></span>
                                    <span className="reading-time font-size-word"><i className="fa fa-clock-o"
                                                                                     aria-hidden="true"/>2-minute read</span>
                                </div>
                                <br/>
                            </div>
                        </header>
                        <div>
                            <h1>Project Description</h1>
                            <p>The objective of the project was to develop a Discord Bot with the requested commands
                                to be used by a Twitch Streamer. The requested commands were about Ticket System,
                                Giveaway System, Moderation and Fun.
                                <br/>
                                <br/>
                                All code developed for this project is available in a repository on my Github Account
                                under the name
                                <a href="https://github.com/GJordao12/Fiverr-DiscordBotAylog"
                                   aria-label="Fiverr Platform"
                                   target="_blank" rel="noreferrer"> Fiverr-DiscordBotAylog</a>.</p>
                            <h2 className="subtitle">Bot Commands</h2>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+supprimer
                                (message quantity 1-99)</mark>: deletes the number of inserted messages from the
                                corresponding text channel;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+avatar</mark>:
                                show your avatar;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+avatar
                                @person</mark>: show the avatar of the person you have identified;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+yt ou
                                +youtube</mark>: show youtube channel;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+twitch</mark>:
                                show twitch channel;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+ban @person
                                raison</mark>: ban someone from the server forever;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+bandoux @person
                                raison</mark>: kick someone from the server and delete all messages;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+kick @person
                                raison</mark>: kick someone from the server;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+ticket</mark>:
                                open a ticket;
                            </p>
                            <p className="text-subtitle"> <mark
                                style={{'background-color': 'grey'}}>+finirticket</mark>: close your ticket;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+giveaway
                                #channel time(10s,10m,10h,10j) winners(1,2,..) price(1,PS5)</mark>: create a giveaway
                                in the channel you want with the time you want with the number of winners you want and
                                the price of the giveaway;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+finirgiveaway
                                messageID</mark>: close the giveaway with the identification message;
                            </p>
                            <p className="text-subtitle"> <mark style={{'background-color': 'grey'}}>+relancer
                                messageID</mark>: draws new winners with the giveaway identification message;
                            </p>

                            <h1>Programming Languages and Tools</h1>
                            <div className="languages-tools">
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={js} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">JavaScript</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={nodejs} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">Node.js</span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1>Media</h1>
                            <img src={discordPicture} alt="alt"/>

                            <h1>Buyer Review</h1>
                            <p>
                                <div>
                                    <span>Communication With Seller</span><span className="stars"></span>
                                </div>
                                <br/>
                                <div>
                                    <span>Service as Described</span><span className="stars"></span>
                                </div>
                                <br/>
                                <div>
                                    <span>Buy Again or Recommend</span><span className="stars"></span>
                                </div>
                            </p>
                        </div>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default DiscordBotAylog;