import { createBrowserRouter, RouterProvider } from 'react-router-dom';

// #region Pages
// import RootLayout from "./pages/RootLayout";

import Error from "./pages/errorPage/ErrorPage";

import Home from "./pages/home/Home";

import AboutMe from "./pages/aboutMe/AboutMe";
import Profile from "./pages/aboutMe/Profile";
import Skills from "./pages/aboutMe/Skills";
import Timeline from "./pages/aboutMe/Timeline";

import Projects from "./pages/projects/Projects";

import FiverrProjects from "./pages/projects/fiverrProjects/FiverrProjects";
import Market from "./pages/projects/fiverrProjects/projects/Market";
import CoursesDatabase from "./pages/projects/fiverrProjects/projects/CoursesDatabase";
import ComputerDatabase from "./pages/projects/fiverrProjects/projects/ComputerDatabase";
import DiscordBotAylog from "./pages/projects/fiverrProjects/projects/DiscordBotAylog";
import TulsaSummerParty from "./pages/projects/fiverrProjects/projects/TulsaSummerParty";
import GenerateNFTs from "./pages/projects/fiverrProjects/projects/GenerateNFTs";
import LandingPageAPIs from "./pages/projects/fiverrProjects/projects/LandingPageAPIs";

import UniversityProjects from "./pages/projects/universityProjects/UniversityProjects";
import WebsiteEarthApp from "./pages/projects/universityProjects/projects/WebsiteEarthApp";
import TimeSeriesAnalysis from "./pages/projects/universityProjects/projects/TimeSeriesAnalysis";
import FreelancerApplication from "./pages/projects/universityProjects/projects/FreelancerApplication";
import PharmaceuticalCompany from "./pages/projects/universityProjects/projects/PharmaceuticalCompany";
import HelpdeskService from "./pages/projects/universityProjects/projects/HelpdeskService";
import InteractiveWatch from "./pages/projects/universityProjects/projects/InteractiveWatch";
import PongGame from "./pages/projects/universityProjects/projects/PongGame";
import SocialNetworkGame from "./pages/projects/universityProjects/projects/SocialNetworkGame";

import PersonalProjects from "./pages/projects/personalProjects/PersonalProjects";
import FlappyBirdGame from "./pages/projects/personalProjects/projects/FlappyBirdGame";
import DiscordBotBugsBronny from "./pages/projects/personalProjects/projects/DiscordBotBugsBronny";
import MyCVWebsite from "./pages/projects/personalProjects/projects/MyCVWebsite";
// #endregion

const router = createBrowserRouter([
    {
        path: "/",
        // element: <RootLayout />,
        errorElement: <Error />,
        children: [
            {
                index: true,
                element: <Home />,
            },
            {
                path: 'about-me',
                children: [
                    {
                        index: true,
                        element: <AboutMe />,
                    },
                    {
                        path: 'profile',
                        element: <Profile />,
                    },
                    {
                        path: 'skills',
                        element: <Skills />,
                    },
                    {
                        path: 'timeline',
                        element: <Timeline />,
                    },
                ],
            },
            {
                path: 'projects',
                children: [
                    {
                        index: true,
                        element: <Projects />,
                    },
                    {
                        path: 'fiverr',
                        children: [
                            {
                                index: true,
                                element: <FiverrProjects />,
                            },
                            {
                                path: 'market',
                                element: <Market />,
                            },
                            {
                                path: 'courses-database',
                                element: <CoursesDatabase />,
                            },
                            {
                                path: 'computer-conference-database',
                                element: <ComputerDatabase />,
                            },
                            {
                                path: 'discord-bot-aylog',
                                element: <DiscordBotAylog />,
                            },
                            {
                                path: 'tulsa-summer-party',
                                element: <TulsaSummerParty />,
                            },
                            {
                                path: 'generate-nfts',
                                element: <GenerateNFTs />,
                            },
                            {
                                path: 'landing-page-apis',
                                element: <LandingPageAPIs />,
                            }
                        ]
                    },
                    {
                        path: 'university',
                        children: [
                            {
                                index: true,
                                element: <UniversityProjects />,
                            },
                            {
                                path: 'website-earth-app',
                                element: <WebsiteEarthApp />,
                            },
                            {
                                path: 'time-series-analysis',
                                element: <TimeSeriesAnalysis />,
                            },
                            {
                                path: 'freelancer-java-application',
                                element: <FreelancerApplication />,
                            },
                            {
                                path: 'pharmaceutical-java-service',
                                element: <PharmaceuticalCompany />,
                            },
                            {
                                path: 'helpdesk-aService',
                                element: <HelpdeskService />,
                            },
                            {
                                path: 'interactive-watch',
                                element: <InteractiveWatch />,
                            },
                            {
                                path: 'pong-game',
                                element: <PongGame />,
                            },
                            {
                                path: 'social-network-game',
                                element: <SocialNetworkGame />,
                            },
                        ]
                    },
                    {
                        path: 'personal',
                        children: [
                            {
                                index: true,
                                element: <PersonalProjects />,
                            },
                            {
                                path: 'flappy-bird-game',
                                element: <FlappyBirdGame />,
                            },
                            {
                                path: 'discord-bot-bugs-bronny',
                                element: <DiscordBotBugsBronny />,
                            },
                            {
                                path: 'my-cv-website',
                                element: <MyCVWebsite />,
                            },
                        ]
                    },
                ]
            },
        ],
    },
]);

export default function App() {
    return <RouterProvider router={router} />;
}