import React from "react";
import Header from "../../../components/header/Header";
import SpecialFooter from "../../../components/specialFooter/SpecialFooter";
import '../../../styles/pages/projects/ProjectsMenu.css'
import Tags from "../../../components/tags/Tags";
import ProjectsMenu from "../../../components/projects/ProjectsMenu";
import ProjectOption from "../../../components/projects/ProjectOption";

function FiverrProjects() {
    return (
        <body class="colorscheme-auto">
            <Tags title={"Fiverr Projects"} />
            <main className="wrapper">
                <Header />
                <ProjectsMenu title="Fiverr Projects" projects={[<ProjectOption date="Sep 28, 2020" path="market" title="Market" />,
                <ProjectOption date="Apr 03, 2021" path="courses-database" title="Courses Database" />,
                <ProjectOption date="Apr 30, 2021" path="computer-conference-database" title="Computer Conference
                                Database"/>,
                <ProjectOption date="Jun 29, 2021" path="discord-bot-aylog" title="Discord Bot Aylog" />,
                <ProjectOption date="Sep 30, 2021" path="tulsa-summer-party" title="Tulsa's Summer Party" />,
                <ProjectOption date="Oct 23, 2021" path="generate-nfts" title="Generate NFTs" />,
                <ProjectOption date="Jan 07, 2022" path="landing-page-apis" title="Landing Page APIs" />]} />
                <SpecialFooter />
            </main>
        </body>
    )
}

export default FiverrProjects;