import React from "react";
import Header from "../../../../components/header/Header";
import SpecialFooter from "../../../../components/specialFooter/SpecialFooter";
import '../../../../styles/pages/projects/ProjectsPage.css'
import coursesDatabase from '../../../../images/fiverr/courses-database.png'
import sql from "../../../../images/skills/sql.png";
import vp from "../../../../images/skills/vp.png";
import Tags from "../../../../components/tags/Tags";

function CoursesDatabase() {
    return (
        <body class="colorscheme-auto">
        <Tags title={"Courses Database"}/>
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container post">
                    <article>
                        <header>
                            <div className="post-title">
                                <h1 className="title">Fiverr - Courses Database</h1>
                            </div>
                            <div className="post-meta">
                                <div className="date">
                                    <span className="posted-on font-size-word"><i className="fa fa-calendar"
                                                                                  aria-hidden="true"/><time
                                        className="font-size-word"
                                        dateTime="2021-03-24T05:52:42-05:00">Apr 03, 2021</time></span>
                                    <span className="reading-time font-size-word"><i className="fa fa-clock-o"
                                                                                     aria-hidden="true"/>2-minute read</span>
                                </div>
                                <br/>
                            </div>
                        </header>
                        <div>
                            <h1>Project Description</h1>
                            <p>The objective of the project was to develop a database for a company where the courses
                                available for them
                                and everything related to them (students, schedules, ...) could be stored. In addition,
                                it was necessary
                                to create queries, procedures and functions so that later, through the requested User
                                Interface, it was
                                possible to observe certain data. It was also requested that the database be loaded with
                                test information,
                                so inserts had to be created.
                                <br/>
                                <br/>
                                All code developed for this project is available in a repository on my Github Account
                                under the name
                                <a href="https://github.com/GJordao12/Fiverr-CoursesDatabase"
                                   aria-label="Fiverr Platform"
                                   target="_blank" rel="noreferrer"> Fiverr-CoursesDatabase</a>.</p>

                            <h1>Programming Languages and Tools</h1>
                            <div className="languages-tools">
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={sql} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">SQL and PL/SQL</span>
                                        </div>
                                    </div>
                                </div>
                                <div
                                    className=" languages-tools card explore-feature border-0 text-center bg-white p-3">
                                    <div className="card-body p-0">
                                        <div
                                            className="skill-icon rounded-circle shadow-lg d-inline-block">
                                            <img src={vp} alt="" className="img-fluid mx-auto"
                                                 style={{display: "revert"}}/>
                                        </div>
                                        <div className="block-center">
                                            <span className="badge skill-badge badge-light">Visual Paradigm</span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <h1>Media</h1>
                            <img src={coursesDatabase} alt="alt"/>

                            <h1>Buyer Review</h1>
                            <p>
                                <div>
                                    <span>Communication With Seller</span><span className="stars"></span>
                                </div>
                                <br/>
                                <div>
                                    <span>Service as Described</span><span className="stars"></span>
                                </div>
                                <br/>
                                <div>
                                    <span>Buy Again or Recommend</span><span className="stars"></span>
                                </div>
                            </p>
                        </div>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default CoursesDatabase;